"use client"

import { useState, useEffect } from "react"
import AdminLayout from "@/components/AdminLayout"
import { Card } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableHeader, TableBody, TableRow, TableHead, TableCell } from "@/components/ui/table"
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts"

const BRANCHES = [
  "Cabang Batu Akik",
  "Cabang Christoper Mihing",
  "Cabang Anang Santawi",
  "Cabang Tidar",
]

interface ProfitLossData {
  id: string
  tanggal: string
  cabang: string
  kategori: string
  jenis: "Pemasukan" | "Pengeluaran"
  jumlah: number
  sumber: "Member" | "Operasional"
}

export default function FinancialReportsPage() {
  const [selectedBranch, setSelectedBranch] = useState<string>("all")
  const [selectedPeriod, setSelectedPeriod] = useState<string>("daily")
  const [profitLossData, setProfitLossData] = useState<ProfitLossData[]>([])

  useEffect(() => {
    const sampleData: ProfitLossData[] = [
      {
        id: "1",
        tanggal: "2024-01-15",
        cabang: "Cabang Batu Akik",
        kategori: "Pendaftaran Member",
        jenis: "Pemasukan",
        jumlah: 50000,
        sumber: "Member"
      },
      {
        id: "2",
        tanggal: "2024-01-15",
        cabang: "Cabang Batu Akik",
        kategori: "Iuran Member",
        jenis: "Pemasukan",
        jumlah: 100000,
        sumber: "Member"
      },
      {
        id: "3",
        tanggal: "2024-01-15",
        cabang: "Cabang Batu Akik",
        kategori: "Penjualan Air Mineral",
        jenis: "Pemasukan",
        jumlah: 25000,
        sumber: "Operasional"
      },
      {
        id: "4",
        tanggal: "2024-01-15",
        cabang: "Cabang Batu Akik",
        kategori: "Listrik",
        jenis: "Pengeluaran",
        jumlah: 75000,
        sumber: "Operasional"
      }
    ]
    setProfitLossData(sampleData)
  }, [])

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("id-ID", {
      style: "currency",
      currency: "IDR",
      minimumFractionDigits: 0,
    }).format(amount)
  }

  const getFilteredData = () => {
    let filtered = [...profitLossData]
    
    if (selectedBranch !== "all") {
      filtered = filtered.filter((item) => item.cabang === selectedBranch)
    }

    // Sort by date
    filtered.sort((a, b) => new Date(a.tanggal).getTime() - new Date(b.tanggal).getTime())

    return filtered
  }

  const calculateSummary = () => {
    const filtered = getFilteredData()
    const totalPemasukan = filtered
      .filter(item => item.jenis === "Pemasukan")
      .reduce((sum, item) => sum + item.jumlah, 0)
    
    const totalPengeluaran = filtered
      .filter(item => item.jenis === "Pengeluaran")
      .reduce((sum, item) => sum + item.jumlah, 0)

    const pemasukanMember = filtered
      .filter(item => item.jenis === "Pemasukan" && item.sumber === "Member")
      .reduce((sum, item) => sum + item.jumlah, 0)

    const pemasukanOperasional = filtered
      .filter(item => item.jenis === "Pemasukan" && item.sumber === "Operasional")
      .reduce((sum, item) => sum + item.jumlah, 0)

    const pengeluaranOperasional = filtered
      .filter(item => item.jenis === "Pengeluaran" && item.sumber === "Operasional")
      .reduce((sum, item) => sum + item.jumlah, 0)

    return {
      totalPemasukan,
      totalPengeluaran,
      labaRugi: totalPemasukan - totalPengeluaran,
      pemasukanMember,
      pemasukanOperasional,
      pengeluaranOperasional
    }
  }

  const prepareChartData = () => {
    const filtered = getFilteredData()
    const data: any[] = []

    filtered.forEach((record) => {
      const existingData = data.find((d) => d.tanggal === record.tanggal)
      
      if (existingData) {
        if (record.jenis === "Pemasukan") {
          existingData.pemasukan += record.jumlah
        } else {
          existingData.pengeluaran += record.jumlah
        }
        existingData.labaRugi = existingData.pemasukan - existingData.pengeluaran
      } else {
        data.push({
          tanggal: record.tanggal,
          pemasukan: record.jenis === "Pemasukan" ? record.jumlah : 0,
          pengeluaran: record.jenis === "Pengeluaran" ? record.jumlah : 0,
          labaRugi: record.jenis === "Pemasukan" ? record.jumlah : -record.jumlah
        })
      }
    })

    return data
  }

  const summary = calculateSummary()

  return (
    <AdminLayout>
      <div className="space-y-8">
        <div>
          <h1 className="text-3xl font-bold">Laporan Laba Rugi</h1>
          <p className="text-gray-500 mt-2">Laporan laba rugi dari seluruh transaksi member dan operasional</p>
        </div>

        {/* Summary Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <Card className="p-6">
            <h3 className="text-sm font-medium text-gray-500">Total Pemasukan</h3>
            <p className="text-2xl font-bold text-green-600 mt-1">
              {formatCurrency(summary.totalPemasukan)}
            </p>
            <div className="mt-4 space-y-1">
              <div className="flex justify-between text-sm">
                <span>Member:</span>
                <span className="font-medium">{formatCurrency(summary.pemasukanMember)}</span>
              </div>
              <div className="flex justify-between text-sm">
                <span>Operasional:</span>
                <span className="font-medium">{formatCurrency(summary.pemasukanOperasional)}</span>
              </div>
            </div>
          </Card>

          <Card className="p-6">
            <h3 className="text-sm font-medium text-gray-500">Total Pengeluaran</h3>
            <p className="text-2xl font-bold text-red-600 mt-1">
              {formatCurrency(summary.totalPengeluaran)}
            </p>
            <div className="mt-4 space-y-1">
              <div className="flex justify-between text-sm">
                <span>Operasional:</span>
                <span className="font-medium">{formatCurrency(summary.pengeluaranOperasional)}</span>
              </div>
            </div>
          </Card>

          <Card className="p-6">
            <h3 className="text-sm font-medium text-gray-500">Laba/Rugi</h3>
            <p className={`text-2xl font-bold mt-1 ${
              summary.labaRugi >= 0 ? "text-green-600" : "text-red-600"
            }`}>
              {formatCurrency(summary.labaRugi)}
            </p>
          </Card>
        </div>

        {/* Filters */}
        <Card className="p-6">
          <div className="flex flex-col md:flex-row gap-4">
            <Select
              value={selectedBranch}
              onValueChange={setSelectedBranch}
            >
              <SelectTrigger className="w-[200px]">
                <SelectValue placeholder="Pilih Cabang" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Semua Cabang</SelectItem>
                {BRANCHES.map((branch) => (
                  <SelectItem key={branch} value={branch}>
                    {branch}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            <Select
              value={selectedPeriod}
              onValueChange={setSelectedPeriod}
            >
              <SelectTrigger className="w-[200px]">
                <SelectValue placeholder="Pilih Periode" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="daily">Harian</SelectItem>
                <SelectItem value="weekly">Mingguan</SelectItem>
                <SelectItem value="monthly">Bulanan</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </Card>

        {/* Chart */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Grafik Laba Rugi</h2>
          <div className="h-[400px]">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={prepareChartData()}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="tanggal" />
                <YAxis />
                <Tooltip formatter={(value) => formatCurrency(Number(value))} />
                <Legend />
                <Line
                  type="monotone"
                  dataKey="pemasukan"
                  stroke="#10b981"
                  name="Pemasukan"
                />
                <Line
                  type="monotone"
                  dataKey="pengeluaran"
                  stroke="#ef4444"
                  name="Pengeluaran"
                />
                <Line
                  type="monotone"
                  dataKey="labaRugi"
                  stroke="#6366f1"
                  name="Laba/Rugi"
                />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </Card>

        {/* Transactions Table */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Detail Transaksi</h2>
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Tanggal</TableHead>
                  <TableHead>Cabang</TableHead>
                  <TableHead>Kategori</TableHead>
                  <TableHead>Sumber</TableHead>
                  <TableHead>Jenis</TableHead>
                  <TableHead>Jumlah</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {getFilteredData().map((item) => (
                  <TableRow key={item.id}>
                    <TableCell>{item.tanggal}</TableCell>
                    <TableCell>{item.cabang}</TableCell>
                    <TableCell>{item.kategori}</TableCell>
                    <TableCell>{item.sumber}</TableCell>
                    <TableCell>
                      <span className={`px-2 py-1 rounded-full text-xs ${
                        item.jenis === "Pemasukan"
                          ? "bg-green-100 text-green-800"
                          : "bg-red-100 text-red-800"
                      }`}>
                        {item.jenis}
                      </span>
                    </TableCell>
                    <TableCell className={item.jenis === "Pemasukan" ? "text-green-600" : "text-red-600"}>
                      {formatCurrency(item.jumlah)}
                    </TableCell>
                  </TableRow>
                ))}
                {profitLossData.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={6} className="text-center text-gray-500">
                      Belum ada data transaksi
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </div>
        </Card>
      </div>
    </AdminLayout>
  )
}
